// stylelint-disable meowtec/no-px
// stylelint-disable scale-unlimited/declaration-strict-value
// stylelint-disable no-descending-specificity
// stylelint-disable declaration-no-important
// stylelint-disable plugin/declaration-block-no-ignored-properties
@use 'sass:math';

$rccs-card-ratio: 1.5858 !default;
$rccs-size: 290px !default;
$rccs-font: 'Consolas', 'Courier', monospace !default;

$rccs-name-font-size: 17px !default;
$rccs-name-font-family: $rccs-font !default;
$rccs-number-font-size: 20px !default;
$rccs-number-font-family: $rccs-font !default;
$rccs-valid-font-size: 10px !default;
$rccs-expiry-font-size: 16px !default;
$rccs-expiry-font-family: $rccs-font !default;
$rccs-cvc-font-size: 14px !default;
$rccs-cvc-font-family: $rccs-font !default;
$rccs-cvc-color: #222 !default;

$rccs-shadow: $box-shadow;
$rccs-light-text-color: $white !default;
$rccs-dark-text-color: $body-color !default;
$rccs-stripe-bg-color: #2a1d16 !default;
$rccs-signature-background: repeating-linear-gradient(
	0.1deg,
	#fff 20%,
	#fff 40%,
	#fea 40%,
	#fea 44%,
	#fff 44%
) !default;

$rccs-default-background: linear-gradient(25deg, #939393, #717171) !default;
$rccs-unknown-background: linear-gradient(25deg, #999, #999) !default;
$rccs-background-transition: $transition-base;

$rccs-animate-background: true;

/** ISSUERS **/
$rccs-amex-background: linear-gradient(25deg, $success, shift-color($success, 30)) !default;
$rccs-dankort-background: linear-gradient(25deg, #ccc, #999) !default;
$rccs-dinersclub-background: linear-gradient(25deg, #fff, #eee) !default;
$rccs-discover-background: linear-gradient(25deg, #fff, #eee) !default;
$rccs-mastercard-background: linear-gradient(25deg, $danger, $warning) !default;
$rccs-visa-background: linear-gradient(25deg, $info, shift-color($info, 15)) !default;
$rccs-elo-background: linear-gradient(25deg, #211c18, #aaa7a2) !default;
$rccs-hipercard-background: linear-gradient(25deg, #8b181b, #de1f27) !default;

/** Images **/
$rccs-chip-image: 'data:image/svg+xml;base64,' !default;

$rccs-amex-logo: 'data:image/svg+xml;base64,' !default;

$rccs-dankort-logo: 'data:image/svg+xml;base64,' !default;

$rccs-dinersclub-logo: 'data:image/svg+xml;base64,' !default;

$rccs-discover-logo: 'data:image/svg+xml;base64,' !default;

$rccs-elo-logo: 'data:image/svg+xml;base64,' !default;

$rccs-hipercard-logo: 'data:image/svg+xml;base64,' !default;

$rccs-jcb-logo: 'data:image/svg+xml;base64,' !default;

$rccs-laser-logo: 'data:image/svg+xml;base64,' !default;

$rccs-maestro-logo: 'data:image/svg+xml;base64,' !default;

$rccs-mastercard-logo: 'data:image/svg+xml;base64,' !default;

$rccs-unionpay-logo: 'data:image/svg+xml;base64,' !default;

$rccs-visa-logo: 'data:image/svg+xml;base64,' !default;

$rccs-visaelectron-logo: 'data:image/svg+xml;base64,' !default;

.rccs {
	width: $rccs-size;
	margin: 0 auto;
	perspective: 1000px;

	&__card {
		position: relative;
		width: $rccs-size;
		height: math.div($rccs-size, $rccs-card-ratio);
		margin: 0 auto;
		transform-style: preserve-3d;
		transition: all 0.4s linear;

		&--front,
		&--back {
			position: absolute;
			top: 0;
			left: 0;
			overflow: hidden;
			width: 100%;
			height: 100%;
			backface-visibility: hidden;
			background: $rccs-default-background;
			border-radius: $rccs-size * 0.05;
			color: $rccs-light-text-color;
			transform-style: preserve-3d;

			@if $rccs-shadow != '' {
				box-shadow: $rccs-shadow;
			}
		}

		&--front {
			z-index: 20;
		}

		&--back {
			transform: rotateY(180deg);

			.rccs__issuer {
				top: auto;
				right: auto;
				bottom: 5%;
				left: 50%;
				background-position: bottom center;
				opacity: 0.6;
				transform: translateX(-50%);
			}
		}

		&__background {
			position: absolute;
			top: -60%;
			left: -170%;
			width: 150%;
			height: 200%;
			transform: rotate(25deg);
			transition: $rccs-background-transition;

			@if $rccs-animate-background == true {
				left: -170%;
			} @else {
				left: -22%;
			}
		}

		&--flipped {
			transform: rotateY(180deg);

			.rccs__card--front {
				z-index: 10;
			}

			.rccs__card--back {
				z-index: 20;
			}
		}

		&--unknown {
			> div {
				background: $rccs-unknown-background;
				box-shadow: none;
			}

			.rccs__issuer {
				visibility: hidden;
			}
		}

		&:not(&--unknown) {
			.rccs__card__background {
				left: -22%;
			}
		}

		&--amex {
			.rccs__card__background {
				background: $rccs-amex-background;
			}

			.rccs__issuer {
				background-image: url($rccs-amex-logo);
			}

			.rccs__cvc__front {
				opacity: 0.5;
				visibility: visible;
			}
		}

		&--dankort {
			.rccs__card__background {
				background: $rccs-dankort-background;
			}

			.rccs__issuer {
				background-image: url($rccs-dankort-logo);
			}
		}

		&--dinersclub {
			> div {
				color: $rccs-dark-text-color;
			}

			.rccs__card__background {
				background: $rccs-dinersclub-background;
			}

			.rccs__issuer {
				background-image: url($rccs-dinersclub-logo);
			}
		}

		&--discover {
			> div {
				color: $rccs-dark-text-color;
			}

			.rccs__card__background {
				background: $rccs-discover-background;
			}

			.rccs__issuer {
				background-image: url($rccs-discover-logo);
			}
		}

		&--elo {
			.rccs__card__background {
				background: $rccs-elo-background;
			}

			.rccs__issuer {
				background-image: url($rccs-elo-logo);
			}
		}

		&--hipercard {
			.rccs__card__background {
				background: $rccs-hipercard-background;
			}

			.rccs__issuer {
				background-image: url($rccs-hipercard-logo);
			}
		}

		&--jcb {
			.rccs__issuer {
				background-image: url($rccs-jcb-logo);
			}
		}

		&--laser {
			.rccs__issuer {
				background-image: url($rccs-laser-logo);
			}
		}

		&--maestro,
		&--mastercard {
			> div {
				color: $rccs-dark-text-color;
			}

			.rccs__card__background {
				background: $rccs-mastercard-background;
			}
		}

		&--maestro {
			.rccs__issuer {
				background-image: url($rccs-maestro-logo);
			}
		}

		&--mastercard {
			.rccs__issuer {
				background-image: url($rccs-mastercard-logo);
			}
		}

		&--unionpay {
			.rccs__issuer {
				background-image: url($rccs-unionpay-logo);
			}
		}

		&--visa,
		&--visaelectron {
			.rccs__card__background {
				background: $rccs-visa-background;
			}

			.rccs__issuer {
				background-size: 75%;
			}
		}

		&--visa {
			.rccs__issuer {
				background-image: url($rccs-visa-logo);
			}
		}

		&--visaelectron {
			.rccs__issuer {
				background-image: url($rccs-visaelectron-logo);
			}
		}
	}

	&__number {
		position: absolute;
		top: 45%;
		left: 10%;
		clear: both;
		font-family: $rccs-number-font-family;
		font-size: $rccs-number-font-size;

		&.rccs__number--large {
			font-size: $rccs-number-font-size * 0.85;
		}
	}

	&__name {
		position: absolute;
		bottom: 15%;
		left: 10%;
		overflow: hidden;
		width: 60%;
		font-family: $rccs-name-font-family;
		font-size: $rccs-name-font-size;
		line-height: 1;
		text-align: left;
		text-overflow: ellipsis;
		text-transform: uppercase;
	}

	&__expiry {
		position: absolute;
		right: 10%;
		bottom: 15%;
		font-size: 0;
		line-height: 1;

		> * {
			vertical-align: middle;
		}

		&__valid {
			margin-bottom: 5px;
			font-size: $rccs-valid-font-size;
		}

		&__value {
			font-family: $rccs-expiry-font-family;
			font-size: $rccs-expiry-font-size;
		}
	}

	&__number,
	&__name,
	&__expiry,
	&__cvc {
		opacity: 0.5;
		transition: opacity 0.3s;
	}

	&__chip {
		position: absolute;
		top: 10%;
		left: 10%;
		width: math.div($rccs-size, 7);
		height: math.div($rccs-size, 11);
		background-image: url($rccs-chip-image);
		background-repeat: no-repeat;
		background-size: contain;
	}

	&__issuer {
		position: absolute;
		top: 10%;
		right: 10%;
		width: 40%;
		height: 23%;
		background-position: top right;
		background-repeat: no-repeat;
		background-size: contain;
	}

	&__stripe {
		position: absolute;
		top: 9%;
		left: 0;
		width: 100%;
		height: 22%;
		background-color: $rccs-stripe-bg-color;
	}

	&__signature {
		position: absolute;
		top: 35%;
		left: 5%;
		width: 75%;
		height: 18%;
		background: $rccs-signature-background;
	}

	&__cvc {
		position: absolute;
		top: 42%;
		left: 67%;
		color: $rccs-cvc-color;
		font-family: $rccs-cvc-font-family;
		font-size: $rccs-cvc-font-size;
		line-height: 1;
	}

	&__cvc__front {
		position: absolute;
		top: 38%;
		right: 10%;
		font-family: $rccs-cvc-font-family;
		font-size: $rccs-cvc-font-size * 0.85;
		opacity: 0;
		visibility: hidden;
	}

	&--filled {
		opacity: 0.8 !important;
	}

	&--focused {
		font-weight: 700;
		opacity: 1 !important;
	}
}

// 3d Shadow
.rccs-shadow-3d {
	@each $state, $value in $theme-colors {
		&-#{$state} {
			.rccs {
				border-radius: $rccs-size * 0.05;
			}

			.rccs::before {
				background: rgba($value, 0.3);
			}

			.rccs::after {
				background: rgba($value, 0.15);
			}
		}
	}
}

// Payment-type
[class*='payment-type-'] {
	height: 3rem;
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
}

.payment-type-amex {
	background-image: url($rccs-amex-logo);
}

.payment-type-dankort {
	background-image: url($rccs-dankort-logo);
}

.payment-type-dinersclub {
	background-image: url($rccs-dinersclub-logo);
}

.payment-type-discover {
	background-image: url($rccs-discover-logo);
}

.payment-type-elo {
	background-image: url($rccs-elo-logo);
}

.payment-type-hipercard {
	background-image: url($rccs-hipercard-logo);
}

.payment-type-jcb {
	background-image: url($rccs-jcb-logo);
}

.payment-type-laser {
	background-image: url($rccs-laser-logo);
}

.payment-type-maestro {
	background-image: url($rccs-maestro-logo);
}

.payment-type-mastercard {
	background-image: url($rccs-mastercard-logo);
}

.payment-type-unionpay {
	background-image: url($rccs-unionpay-logo);
}

.payment-type-visa {
	background-image: url($rccs-visa-logo);
	filter: contrast(0.5);
}

.payment-type-visaelectron {
	background-image: url($rccs-visaelectron-logo);
}

// stylelint-enable meowtec/no-px
// stylelint-enable scale-unlimited/declaration-strict-value
// stylelint-enable no-descending-specificity
// stylelint-enable declaration-no-important
// stylelint-enable plugin/declaration-block-no-ignored-properties
